.loader {
  letter-spacing: 2px;
  font-size: 26px;
  position: relative;
}

.loader:before {
  content: "";
  color: #fff;
}

.loader:after {
  content: "";
  width: 20px;
  height: 20px;
  background-color: #06bfa0;
  border-radius: 50%;
  margin: auto;
  animation: motion 3s ease-in-out infinite;
  position: absolute;
  top: -70px;
  bottom: 0;
  left: 0;
  right: 0;
}

@keyframes motion {
  0%, 50%, 100% {
    transform: translateX(0)scale(1);
  }

  25% {
    transform: translateX(-100px)scale(.3);
  }

  75% {
    transform: translateX(100px)scale(.3);
  }
}

.breed-select {
  border: 2px solid #18f0d7;
  border-radius: 20px;
  padding: 8px 13px;
}

img {
  width: 100%;
  height: auto;
  margin-top: 30px;
  display: block;
}

.container {
  max-width: 600px;
  padding: 10px;
}

